	module.exports = {"pathContext":{"docHtml":"<h1>1.0.0 - 2016-01-07</h1>\n<ul>\n<li>Removed: unused speed test</li>\n<li>Added: Automatic routing between previously unsupported conversions\n(<a href=\"https://github.com/Qix-/color-convert/pull/27\">#27</a>)</li>\n<li>Removed: <code>xxx2xxx()</code> and <code>xxx2xxxRaw()</code> functions\n(<a href=\"https://github.com/Qix-/color-convert/pull/27\">#27</a>)</li>\n<li>Removed: <code>convert()</code> class\n(<a href=\"https://github.com/Qix-/color-convert/pull/27\">#27</a>)</li>\n<li>Changed: all functions to lookup dictionary\n(<a href=\"https://github.com/Qix-/color-convert/pull/27\">#27</a>)</li>\n<li>Changed: <code>ansi</code> to <code>ansi256</code>\n(<a href=\"https://github.com/Qix-/color-convert/pull/27\">#27</a>)</li>\n<li>Fixed: argument grouping for functions requiring only one argument\n(<a href=\"https://github.com/Qix-/color-convert/pull/27\">#27</a>)</li>\n</ul>\n<h1>0.6.0 - 2015-07-23</h1>\n<ul>\n<li>\n<p>Added: methods to handle\n<a href=\"https://en.wikipedia.org/wiki/ANSI_escape_code#Colors\">ANSI</a> 16/256 colors:</p>\n<ul>\n<li>rgb2ansi16</li>\n<li>rgb2ansi</li>\n<li>hsl2ansi16</li>\n<li>hsl2ansi</li>\n<li>hsv2ansi16</li>\n<li>hsv2ansi</li>\n<li>hwb2ansi16</li>\n<li>hwb2ansi</li>\n<li>cmyk2ansi16</li>\n<li>cmyk2ansi</li>\n<li>keyword2ansi16</li>\n<li>keyword2ansi</li>\n<li>ansi162rgb</li>\n<li>ansi162hsl</li>\n<li>ansi162hsv</li>\n<li>ansi162hwb</li>\n<li>ansi162cmyk</li>\n<li>ansi162keyword</li>\n<li>ansi2rgb</li>\n<li>ansi2hsl</li>\n<li>ansi2hsv</li>\n<li>ansi2hwb</li>\n<li>ansi2cmyk</li>\n<li>ansi2keyword\n(<a href=\"https://github.com/harthur/color-convert/pull/18\">#18</a>)</li>\n</ul>\n</li>\n</ul>\n<h1>0.5.3 - 2015-06-02</h1>\n<ul>\n<li>Fixed: hsl2hsv does not return <code>NaN</code> anymore when using <code>[0,0,0]</code>\n(<a href=\"https://github.com/harthur/color-convert/issues/15\">#15</a>)</li>\n</ul>\n<hr>\n<p>Check out commit logs for older releases</p>","docPath":"argo-ci/node_modules/color-convert/changelog","proj":"argo-ci"}}

